<template>
    <div class="Layout">
        <Viwe>
            <h2 slot="name" >Dialog </h2>
            <p slot="title" ></p>
        </Viwe>
        <Viwe>
            <h3 slot="name" ></h3>
            <p slot="title" >Dialog </p>
            <div slot="content">
                <div class="demo-block" >
                    <div class="source">
                        <sh-button type="text" @click="dialogVisible = true"> Dialog</sh-button>
                        <sh-dialog
                        title=""
                        :visible.sync="dialogVisible"
                        width="30%">
                            <span></span>
                            <span slot="footer" class="dialog-footer">
                                <sh-button @click="dialogVisible = false"> </sh-button>
                                <sh-button type="primary" @click="dialogVisible = false"> </sh-button>
                            </span>
                        </sh-dialog>
                    </div>
                    <collapse-transition>
                        <div class="meta" v-show="isActive">
                            <!-- @slot default -->
                            <div class="collapse-wrap">
                                <div class="description">
                                    <p>visibleBooleantrue DialogDialog bodyfooterfooterfooterslottitle</p>
                                </div>
                                <div class="highlight">
                                    <pre>
                                        <code>
&lt;ShButton type=&quot;primary&quot; @click=&quot;visible = true&quot;&gt;&lt;/ShButton&gt;
      &lt;ShDialog width=&quot;20%&quot; top=&quot;200px&quot; :visible.sync=&quot;visible&quot;&gt;
        &lt;ul&gt;
          &lt;li&gt;1&lt;/li&gt;
          &lt;li&gt;1&lt;/li&gt;
          &lt;li&gt;1&lt;/li&gt;
        &lt;/ul&gt;
        &lt;template v-slot:footer&gt;
          &lt;ShButton type=&quot;primary&quot; @click=&quot;visible = false&quot;&gt;&lt;/ShButton&gt;
          &lt;ShButton @click=&quot;visible = false&quot;&gt;&lt;/ShButton&gt;
        &lt;/template&gt;
      &lt;/ShDialog&gt;
&lt;script&gt;

export default {
  name: &#x27;App&#x27;,
  data(){
    return {
      visible: false
    }
  },
  methods: {
  }
}
&lt;/script&gt;
                                        </code>
                                    </pre>
                                </div>
                            </div>
                        </div>
                    </collapse-transition>
                    <div class="demo-block-control">
                        <i v-show="!isActive" class="sh-icon-caret-bottom"></i>
                        <i v-show="isActive" class="sh-icon-caret-top"></i>
                        <span @click="isActive = !isActive">{{ isActive?'':'' }}</span>
                    </div>
                </div>
            </div>
        </Viwe>
    </div>
</template>

<script>
import Viwe from '../untils/viwe'
import collapseTransition from "../../../packages/js/utils/animation"
export default {
  name: 'Dialog',
  data(){
    return {
        isActive: false,
        dialogVisible: false,
    }
  },
  components: {
      Viwe,
      collapseTransition
  },
  methods: {
  }
}
</script>

<style lang="scss" scoped>
@import '../../style/mianrigth.scss';
</style>



