// vuevuex
import Vue from 'vue'
import Vuex from 'vuex'
// vuex
Vue.use(Vuex)
 
// vuex
export default new Vuex.Store({
  // 
  state: {
    indexID: 12,
  },
  // , ,this.$store.commit("xxx")
  mutations: {
    changeDataMut(state,params){  
        state.indexID = params
    }
  },
  //  ,this.$store.dispatch("xxx") 
  actions: {
  },
  // Vuex,
  getters: {
  },
  // 
  modules: {
  }
})


