<template>
    <div class="Layout">
        <Viwe>
            <h2 slot="name" >Input </h2>
            <p slot="title" >
            Input  Vue 

 input v-model

 v-model </p>
        </Viwe>
        <Viwe>
            <h3 slot="name" ></h3>
