                    </collapse-transition>
                    <div class="demo-block-control">
                        <i v-show="!isActive1" class="sh-icon-caret-bottom"></i>
                        <i v-show="isActive1" class="sh-icon-caret-top"></i>
                        <span @click="isActive1 = !isActive1">{{ isActive1?'':'' }}</span>
                    </div>
                </div>
            </div>
        </Viwe>
        <Viwe>
            <h3 slot="name" ></h3>
            <div slot="content">
                <div class="demo-block" >
                    <div class="source">
                        <div style="width: 200px;">
                            <Sh-Input v-model="input2" clearable></Sh-Input>
                        </div>
                    </div>
                    <collapse-transition>
                        <div class="meta" v-show="isActive2">
                            <!-- @slot default -->
                            <div class="collapse-wrap">
                                <div class="description">
                                    <p>clearable</p>
                                </div>
                                <div class="highlight">
                                    <pre>
                                        <code>
&lt;div style=&quot;width: 200px;&quot;&gt;
    &lt;Sh-Input v-model=&quot;input&quot; clearable &gt;&lt;/Sh-Input&gt;
&lt;/div&gt;

&lt;script&gt;
export default {
    data() {
        return {
            input: &#x27;&#x27;
        }
    }
}
&lt;/script&gt;
                                        </code>
                                    </pre>
                                </div>
                            </div>
                        </div>
                    </collapse-transition>
                    <div class="demo-block-control">
                        <i v-show="!isActive2" class="sh-icon-caret-bottom"></i>
                        <i v-show="isActive2" class="sh-icon-caret-top"></i>
                        <span @click="isActive2 = !isActive2">{{ isActive2?'':'' }}</span>
                    </div>
                </div>
            </div>
        </Viwe>
        <Viwe>
            <h3 slot="name" ></h3>
            <div slot="content">
                <div class="demo-block" >
                    <div class="source">
                        <div style="width: 200px;">
                            <Sh-Input v-model="input3" show-password ></Sh-Input>
                        </div>
                    </div>
                    <collapse-transition>
                        <div class="meta" v-show="isActive3">
                            <!-- @slot default -->
                            <div class="collapse-wrap">
                                <div class="description">
                                    <p>show-password</p>
                                </div>
                                <div class="highlight">
                                    <pre>
                                        <code>
&lt;div style=&quot;width: 200px;&quot;&gt;
    &lt;Sh-Input v-model=&quot;input&quot; show-password &gt;&lt;/Sh-Input&gt;
&lt;/div&gt;

&lt;script&gt;
export default {
    data() {
        return {
            input: &#x27;&#x27;
        }
    }
}
&lt;/script&gt;
                                        </code>
                                    </pre>
                                </div>
                            </div>
                        </div>
                    </collapse-transition>
                    <div class="demo-block-control">
                        <i v-show="!isActive3" class="sh-icon-caret-bottom"></i>
                        <i v-show="isActive3" class="sh-icon-caret-top"></i>
                        <span @click="isActive3 = !isActive3">{{ isActive3?'':'' }}</span>
                    </div>
                </div>
            </div>
        </Viwe>
    </div>
</template>

<script>
import Viwe from '../untils/viwe'
import collapseTransition from "../../../packages/js/utils/animation"
export default {
  name: 'Input',
  data(){
    return {
        isActive: false,
        isActive1: false,
        isActive2: false,
        isActive3: false,
        input: '',
        input1: '',
        input2: '',
        input3: '',
    }
  },
  components: {
      Viwe,
      collapseTransition
  },
  methods: {
  }
}
</script>

<style lang="scss" scoped>
@import '../../style/mianrigth.scss';
</style>



