import '../css/row.scss'
export default {
  name: 'ShRow', // button 
  componentName: 'ShRow', // col   col  gutter 
  props: {
    tag: { //, tag  render 
        // vue is  ps: <p is="span"></p> span
        //C SEO, HTML  tag
      type: String,
      default: 'div'
    },
    gutter: Number, //
    type: String, //  flex
    justify: { // flex 
      type: String,
      default: 'start'
    },
    align: { //flex 
      type: String,
      default: 'top'
    }
  },

  computed: {
    style() {
      const ret = {};
      if (this.gutter) {
        ret.marginLeft = `-${this.gutter / 2}px`;
        ret.marginRight = ret.marginLeft;
      }
      return ret;
    }
  },

  render(h) {  //render 
    return h(this.tag, {
      class: [
        'sh-row',
        this.justify !== 'start' ? `is-justify-${this.justify}` : '',
        this.align !== 'top' ? `is-align-${this.align}` : '',
        { 'sh-row--flex': this.type === 'flex' } // flex 
      ],
      style: this.style //object  
    }, this.$slots.default); //
  }
};


